.card {
        padding: 10px;
        max-width: 260px;
        box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
        background-color: rgb(255, 255, 255);
        font-family: sans-serif;
      }
      
      .content {
        margin-top: 1rem;
      }
      
      .content .header {
        margin-top: 0.5rem;
      }
      
      .image {
        width: 100%;
        height: 150px;
        border-radius: 4px;
        background-color: rgb(205, 235, 255);
        padding: 9px;
      }
      
      .tag {
        text-transform: capitalize;
        background-color: rgba(0, 0, 0, 0.425);
        padding: 2px 8px;
        border-radius: 4px;
        color: #fff;
      }
      
      .date {
        display: flex;
        font-size: 0.75rem;
        line-height: 1rem;
        align-items: center;
        margin: 2px;
      }
      
      .title {
        display: block;
        font-size: 1.25rem;
        line-height: 1.35rem;
        font-weight: 600;
        padding-bottom: 5px;
        padding-top: 14px;
      }
      
      .description {
        line-height: 1.375;
      }

      .info a{
        text-decoration: none;
        color:#3d0b0b;
      }
      .info a:hover{
        
        color:#eb0c0c;
      }

      button {
        position: relative;
        display: inline-block;
        cursor: pointer;
        outline: none;
        border: 0;
        vertical-align: middle;
        text-decoration: none;
        background: transparent;
        padding: 0;
        font-size: inherit;
        font-family: inherit;
       }
       
       button.read-more {
        width: 12rem;
        height: auto;
       }
       
       button.read-more .circle {
        transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
        position: relative;
        display: block;
        margin: 0;
        width: 3rem;
        height: 3rem;
        background: #282936;
        border-radius: 1.625rem;
       }
       
       button.read-more .circle .icon {
        transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        background: #fff;
       }
       
       button.read-more .circle .icon.arrow {
        transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
        left: 0.625rem;
        width: 1.125rem;
        height: 0.125rem;
        background: none;
       }
       
       button.read-more .circle .icon.arrow::before {
        position: absolute;
        content: "";
        top: -0.29rem;
        right: 0.0625rem;
        width: 0.625rem;
        height: 0.625rem;
        border-top: 0.125rem solid #fff;
        border-right: 0.125rem solid #fff;
        transform: rotate(45deg);
       }
       
       button.read-more .button-text {
        transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 0.75rem 0;
        margin: 0 0 0 1.85rem;
        color: #282936;
        font-weight: 700;
        line-height: 1.6;
        text-align: center;
        text-transform: uppercase;
       }
       
       button:hover .circle {
        width: 100%;
       }
       
       button:hover .circle .icon.arrow {
        background: #fff;
        transform: translate(1rem, 0);
       }
       
       button:hover .button-text {
        color: #fff;
       }